@font-face {
  font-family: "80sFont";
  src: url(../fonts/amstrad_cpc464.ttf);
}
body {
  background-color: #0100ad;
  color: #ecec04;
  font-family: "80sFont", Verdana, sans-serif;
}

p {
  margin: 0px;
}

h1 {
  border-bottom: 1px solid #4cbda3;
  margin-left: 1em;
  margin-right: 1em;
}

.flex {
  display: flex;
}
.flexHorizontal {
  flex-direction: row;
}

.bannerContainer {
  justify-content: space-between;
  margin: 1rem;
}
.bannerTextLogo {
  font-family: monospace;
  margin-left: 1rem;
}
.bannerMenuElt {
  font-family: "80sFont", Verdana, sans-serif;
  font-size: larger;
  margin-left: 1.5rem;
  text-decoration: none;
  color: #ecec04;
  padding-bottom: 0.5em;
}
.bannerMenuElt:hover {
  border-bottom: 1px solid #ecec04;
}

.defaultFont {
  color: #ecec04;
  font-family: "80sFont", Verdana, sans-serif;
  font-size: small;
}

.toolbarContainer {
  margin-top: 5px;
}

.listButton {
  color: #ecec04;
  background-color: darkgreen;
  border-radius: 5px;
  font-family: "80sFont", Verdana, sans-serif;
  font-size: small;
  padding: 5px;
  margin-right: 1rem;
  cursor: pointer;
}
.listButton:hover {
  color: darkgreen;
  background-color: #ecec04;
}

.login-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.login-wrapper input {
  margin-top: 0.5em;
  margin-bottom: 1em;
  min-height: 2em;
  color: darkgreen;
  font-family: "80sFont", Verdana, sans-serif;
}

.fade-out-message {
  background-color: aqua;
  color: red;
  opacity: 1;
  transition: opacity 1s ease;
  margin-bottom: 1em;
  border-radius: 0.5em;
  padding: 0.5em;
  margin-left: 1em;
  margin-right: 1em;
}

.fade-out-message.fade-out {
  opacity: 0;
  transition: opacity 1s ease;
}

.error {
  color: red;
  margin: 1em;
  padding: 1em;
  background-color: lightcoral;
}/*# sourceMappingURL=main.css.map */